}

/* The search field */
#myInput {
 box-sizing: border-box;
 background-position: 14px 12px;
 background-repeat: no-repeat;
 font-size: 16px;
 padding: 14px 20px 12px 45px;
 border: none;
 border-bottom: 1px solid #ddd;
}

/* The search field when it gets focus/clicked on */
